

#about {
  // background-color: $white-color;
  // color: $dark-grey;
    background-color: $primary-color;
    background-image: linear-gradient(
      135deg,
      $primary-color 0%,
      $secondary-color 100%
    );
    color: $white-color;
    //height: 100%;
    border-top: 0px;
    -webkit-clip-path: polygon(0 0, 100% 0, 100% 80%, 0 100%);
    clip-path: polygon(0 0, 100% 0, 100% 80%, 0 100%);
    padding-bottom: 10%;
  
    @include respond(tab-land) {
      //height: 100%;
      -webkit-clip-path: none;
      clip-path: none;
    }
  
    .about-wrapper {
      @include respond(phone) {
        padding-bottom: 5rem;
      }
  
      // &__image {
      //   display: flex;
      //   height: 100%;
      //   align-items: center;
      //   justify-content: center;
  
      //   @include respond(tab-port) {
      //     padding-bottom: 6.4rem;
      //   }
      //   @include respond(tab-land) {
      //     height: 100%;
      //   }
      // }
  
      &__info {
        display: flex;
        height: 100%;
        justify-content: center;
        text-align: left;
        flex-direction: column;
  
        @include respond(phone) {
          align-items: center;
        }
  
        &-text {
          text-align: left;
  
          @include respond(tab-port) {
            font-size: 1.5rem;
            text-align: left;
          }
          @include respond(phone) {
            font-size: 1.6rem;
            text-align: left;
          }
  
          &--important {
            background: darken($secondary-color, 1%);
            display: inline-block;
            font-style: italic;
            padding: 0 0.3rem;
            margin: 0.3rem 0;
            line-height: 1.6;
  
            @include respond(phone) {
              display: inline;
              margin: 0;
              padding: 0;
              background: transparent;
              font-style: normal;
            }
          }
        }
      }
    }
    .info-wrapper{
      margin-left: auto;
      margin-right: auto;
      
    }
    
  }
  
  .md-center{
    @include respond(tab-land){
      justify-content: center;
  
    }
  }
  